.l-footer {
    display: -ms-flexbox;
    display: flex;
    background-color: var(--g-color-secondary);
    border-top: 1px solid var(--g-color-tertiary);
    bottom: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    min-height: var(--g-spacing-3x-large);
    z-index: 2;
}

.c-footer__buttons {
    background-color: var(--g-color-secondary);
    border-top: 1px solid var(--g-color-tertiary);
    color: black;
    position: fixed;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    right: 0;
    left: 0;
    height: var(--g-spacing-3x-large);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    bottom: calc(var(--g-spacing-3x-large) * -1);
    z-index: 4;
}

.c-footer__buttons.is-active {
    -webkit-animation: slide-up 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
            animation: slide-up 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

.c-footer__buttons.is-disabled {
    -webkit-animation: slide-down 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
            animation: slide-down 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}



