.c-group-container {
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
}

.c-group-container.is-pushed-right {
    padding-left: var(--g-spacing-2x-large);
    -webkit-transition: padding-left 0.3s ease-out;
    -o-transition: padding-left 0.3s ease-out;
    transition: padding-left 0.3s ease-out;
}

.c-group-container.is-pushed-left {
    padding-left: 0;
    -webkit-transition: padding-left 0.3s ease-out;
    -o-transition: padding-left 0.3s ease-out;
    transition: padding-left 0.3s ease-out;
}

.c-group-container.is-pushed-left .c-group__placeholder {
    opacity: 0 !important;
    -webkit-transition: opacity 0.3s ease-out;
    -o-transition: opacity 0.3s ease-out;
    transition: opacity 0.3s ease-out;
}

.c-group-container.is-pushed-right .c-group__placeholder {
    opacity: 1 !important;
    -webkit-transition: opacity 0.3s ease-out;
    -o-transition: opacity 0.3s ease-out;
    transition: opacity 0.3s ease-out;
}

.c-group__placeholder {
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    width: var(--g-spacing-2x-large);
    opacity: 0;
}
.c-group-item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-positive: 1;
        flex-grow: 1;
    position: relative;
}

.c-group-item__inner {
    border-bottom: 1px solid var(--g-color-tertiary);
    padding: var(--g-spacing-x-small);
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.c-group-item__image {
    border: 1px solid var(--g-color-tertiary);
    border-radius: 50%;
    height: var(--g-spacing-3x-large);
    width: var(--g-spacing-3x-large);
    background-color: var(--g-color-active);
    margin: var(--g-spacing-x-small) var(--g-spacing) var(--g-spacing-x-small)
        var(--g-spacing-small);
}

.c-group-item__text {
    color: var(--g-color-tertiary);
    display: -ms-flexbox;
    display: flex;
}

.c-group-item__cover-button {
    bottom: 0;
    height: 100%;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
}



