.c-button-group {
    display: -ms-flexbox;
    display: flex;

    > button:not(:first-child),
    > .c-button:not(:first-child) {
        margin-left: var(--g-spacing);
    }

    > .c-button-icon-only:not(:first-child) {
        margin-left: var(--g-spacing-small);
    }
}

/*
* Modifiers
*/

.c-button-group--right {
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.c-button-group--space-between {
    -ms-flex-pack: justify;
        justify-content: space-between;
}



