html,
body {
    height: 100%;
}
body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

ul,
ol {
    list-style: none;
    margin: 0;
    padding: 0;
}

p {
    margin: 0;
}

a {
    color: var(--g-color-tertiary);
    text-decoration: none;
}

button {
    background: transparent;
    border: 0;
    border-radius: 0;
    margin: 0;
    padding: 0;
    cursor: pointer;
}



