@-webkit-keyframes slide-fwd-center {
    0% {
        -webkit-transform: translateZ(0);
                transform: translateZ(0);
    }
    100% {
        -webkit-transform: translateZ(160px) rotate(45deg);
                transform: translateZ(160px) rotate(45deg);
    }
}

@keyframes slide-fwd-center {
    0% {
        -webkit-transform: translateZ(0);
                transform: translateZ(0);
    }
    100% {
        -webkit-transform: translateZ(160px) rotate(45deg);
                transform: translateZ(160px) rotate(45deg);
    }
}

@-webkit-keyframes opacity-fade-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes opacity-fade-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes slide-bottom {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-400px);
                transform: translateY(-400px);
    }
    10% {
        opacity: 1;
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes slide-bottom {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-400px);
                transform: translateY(-400px);
    }
    10% {
        opacity: 1;
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@-webkit-keyframes slide-right {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    100% {
        -webkit-transform: translateX(60px);
                transform: translateX(60px);
    }
}

@keyframes slide-right {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    100% {
        -webkit-transform: translateX(60px);
                transform: translateX(60px);
    }
}

@-webkit-keyframes slide-left {
    0% {
        -webkit-transform: translateX(60px);
                transform: translateX(60px);
    }
    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes slide-left {
    0% {
        -webkit-transform: translateX(60px);
                transform: translateX(60px);
    }
    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@-webkit-keyframes slide-up {
    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    100% {
        -webkit-transform: translateY(calc(var(--g-spacing-3x-large) * -1));
                transform: translateY(calc(var(--g-spacing-3x-large) * -1));
    }
}

@keyframes slide-up {
    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    100% {
        -webkit-transform: translateY(calc(var(--g-spacing-3x-large) * -1));
                transform: translateY(calc(var(--g-spacing-3x-large) * -1));
    }
}

@-webkit-keyframes slide-down {
    0% {
        -webkit-transform: translateY(calc(var(--g-spacing-3x-large) * -1));
                transform: translateY(calc(var(--g-spacing-3x-large) * -1));
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes slide-down {
    0% {
        -webkit-transform: translateY(calc(var(--g-spacing-3x-large) * -1));
                transform: translateY(calc(var(--g-spacing-3x-large) * -1));
    }
    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

