    0% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
    100% {
        -webkit-transform: translateY(calc(var(--g-viewport-height) * -1));
                transform: translateY(calc(var(--g-viewport-height) * -1));
    }
}

@keyframes slide-right-up {
