.c-nav-item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-positive: 1;
        flex-grow: 1;
    height: auto;
}

.c-nav-item__link {
    -ms-flex-align: center;
        align-items: center;
    color: var(--g-color-tertiary);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-positive: 1;
        flex-grow: 1;
    -ms-flex-pack: center;
        justify-content: center;
    padding-top: 7px;
    border-style: border-box;
    border-top: 1px solid var(--g-color-tertiary);
}

.c-nav-item__icon {
    color: var(--g-color-tertiary);
    pointer-events: none;
}

.c-nav-item__text {
    margin-top: var(--g-spacing-2x-small);
    font-size: 10px;
}

.c-nav-item:hover {
    background-color: var(--g-color-blue-500);
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}

.c-nav-item:hover .c-nav-item__link {
    color: var(--g-color-brown-500);
}

.c-nav-item__link.is-active,
.c-nav-item__link.is-active .c-nav-item__icon {
    color: #aaaa55;
}




